* {
    box-sizing: border-box;
    border: none;
    padding: 0px;
    margin: 0px;
    font-family: 'Montserrat', sans-serif;
}

body, html {
    background-color: #fff;
    margin: 0;
}

.header {
    width: 100%;
    background: -webkit-linear-gradient(rgb(112, 185, 218) 0%, rgb(117, 169, 192) 80%, rgb(255, 255, 255) 80.001%, rgb(255, 255, 255) 100%);
    background: linear-gradient(rgb(112, 185, 218) 0%, rgb(117, 169, 192) 80%, rgb(255, 255, 255) 80.001%, rgb(255, 255, 255) 100%);
    min-height: 750px;
}

.header-container {
    max-width: 650px;
    margin: auto;
    padding-top: 200px;
}

.header-container-image {
    max-width: 850px;
    margin: auto;
    padding-top: 0px;
}

h1 {
    color: #fff;
    font-size: 32px;
    font-weight: 900;
}

h2 {
    color: #fff;
    font-size: 20px;
    font-weight: 500;
    margin-top: 5px;
}

.logo {
    width: 60%;
    width: 200px;
    height: auto;
    position: fixed;
    top: 40px;
    left: 40px;
}

.head-image {
    margin-top: 40px;
    width: 100%;
    height: auto;
    max-height: 500px;
    border-radius: 10px;
    object-fit: cover;
}

.content {
    max-width: 650px;
    margin: auto;
    margin-top: 40px;
    margin-bottom: 40px;
    color: #333;
    font-size: 18px;
    line-height: 26px;
    font-weight: 300;
}

a {
    color: rgb(57, 140, 254);
    text-decoration: none;
    display: block;
    margin-top: 10px;
    margin-bottom: 10px;
    cursor: pointer;
}

.buttonlink {
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 16px;
    font-weight: 600;
    color: rgb(255, 255, 255);
    background-color: rgb(57, 140, 254);
    border-style: solid;
    border-width: 0px;
    border-radius: 56px;
    padding: 15px 40px;
    text-decoration: none;
    cursor: pointer;
}

@media only screen and (max-width: 600px) {
    .header-container {
        padding-left: 40px;
        padding-right: 40px;
        padding-top: 150px;
    }
    .head-image {
        border-radius: 0px;
    }
    .header {
        min-height: 0;
        min-height: initial;
    }
    .content {
        margin-top: 0px;
        padding: 40px;
    }
}
.bugbattle--feedback-dialog-container input, .bugbattle--feedback-dialog-container label {
  margin: 0;
  padding: 0;
  border: none;
  background-color: #fff;
}

.bugbattle--feedback-button {
  padding: 0px;
  margin: 0px;
  position: fixed;
  top: calc(50% - 58px);
  right: 0px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  cursor: pointer;
  background-color: #398cfe;
  color: #fff;
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.15);
  z-index: 916777260;
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
}

.bugbattle--feedback-button:hover {
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);
}

.bugbattle--feedback-button-inner {
  position: relative;
  width: 38px;
  height: 116px;
  padding: 0px;
  margin: 0px;
  position: relative;
}

.bugbattle--feedback-button-inner-text {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%) rotate(-90deg);
          transform: translateX(-50%) translateY(-50%) rotate(-90deg);
  background: none;
  text-align: center;
  font-weight: bold;
  font-size: 16px;
  font-family: sans-serif;
}

.bugbattle--feedback-dialog-container {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100vw;
  height: 100vh;
  height: -webkit-fill-available;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 916777282;
  background-color: rgba(105, 117, 129, 0.5);
}

.bugbattle--feedback-dialog {
  width: 90%;
  max-width: 400px;
  background-color: #fff;
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  overflow-y: auto;
  box-sizing: border-box;
}

.bugbattle--feedback-dialog-header {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 32px;
  padding-top: 32px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  box-sizing: border-box;
}

.bugbattle--feedback-dialog-header-button-cancel svg {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.bugbattle--feedback-dialog-header-button-cancel:hover svg {
  opacity: 0.6;
}

.bugbattle--feedback-dialog-header-title {
  font-weight: bold;
  text-align: center;
  color: #000;
  font-size: 16px;
  font-family: sans-serif;
  text-align: center;
}

.bugbattle--feedback-dialog-info-text {
  color: #666;
  font-size: 18px;
  font-family: sans-serif;
  padding-top: 10px;
  text-align: center;
}

.bugbattle--feedback-dialog-success {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 50px;
  display: none;
}

.bugbattle--feedback-dialog-success svg {
  width: 30px;
  height: auto;
}

.bugbattle--feedback-dialog-loading {
  display: none;
}

.bugbattle--feedback-dialog-body {
  display: block;
}

.bugbattle--feedback-inputgroup {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  padding: 0px 32px;
  flex-direction: column;
}

.bugbattle--feedback-inputgroup-button {
  display: flex;
  align-items: center;
}

.bugbattle--feedback-inputgroup-text {
  color: #000;
  font-size: 16px;
  font-weight: normal;
  font-family: sans-serif;
}

.bugbattle--feedback-inputgroup-label {
  padding: 0px;
  margin-bottom: 5px;
  color: #000;
  font-size: 14px;
  font-weight: 600;
  font-family: sans-serif;
}

.bugbattle--feedback-send-button {
  background-color: #398cfe;
  border-radius: 8px;
  box-sizing: border-box;
  padding: 14px 20px;
  font-size: 16px;
  font-weight: bold;
  font-family: sans-serif;
  color: #fff;
  text-align: center;
  width: auto;
  margin-top: 5px;
  margin-bottom: 20px;
  cursor: pointer;
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.15);
}

.bugbattle--feedback-send-button:hover {
  opacity: 0.9;
}

.bugbattle--feedback-inputgroup>input, .bugbattle--feedback-inputgroup input {
  width: auto;
  padding: 15px 12px;
  font-size: 15px;
  font-family: sans-serif;
  outline: none;
  border: 1px solid #e9f1fd;
  background-color: #fafbfd;
  border-radius: 8px;
  box-sizing: border-box;
}

.bugbattle--feedback-inputgroup textarea {
  outline: none;
  box-shadow: none;
  resize: none;
  width: auto;
  padding: 12px;
  font-size: 15px;
  line-height: 17px;
  font-family: sans-serif;
  margin: 0px;
  box-sizing: border-box;
  border: 1px solid #e9f1fd;
  background-color: #fafbfd;
  border-radius: 8px;
  box-sizing: border-box;
}

.bugbattle--feedback-inputgroup--privacy-policy {
  padding: 8px 20px;
  font-family: sans-serif;
  font-size: 14px;
  flex-direction: row;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
}

.bugbattle--feedback-inputgroup--privacy-policy-label {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 5px;
  white-space: pre;
}

.bugbattle--feedback-inputgroup--privacy-policy a {
  color: #398cfe;
}

.bugbattle--feedback-inputgroup--privacy-policy input {
  width: auto;
  border: none;
  padding: 20px;
  font-size: 15px;
  font-family: sans-serif;
  outline: none;
}

.bugbattle-feedback-importance {
  margin: 20px;
  margin-bottom: -8px;
  font-weight: bold;
  color: #222426;
  font-size: 14px;
  font-family: sans-serif;
}

.bugbattle--feedback-image {
  position: relative;
}

.bugbattle--feedback-poweredbycontainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -10px;
  margin-bottom: 20px;
  cursor: pointer;
}

.bugbattle--feedback-poweredbycontainer span {
  font-weight: normal;
  font-size: 14px;
  font-family: sans-serif;
  color: #aaa;
}

.bugbattle--feedback-poweredbycontainer svg {
  height: 18px;
  width: auto;
  margin-left: 5px;
}

.bugbattle--edit-button {
  position: absolute;
  top: calc(50% - 25px);
  left: 100px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.45);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bugbattle--edit-button svg {
  width: 60%;
}

.bugbattle-spinner {
  width: 40px;
  height: 40px;
  position: relative;
  margin: 100px auto;
}

.bugbattle-double-bounce1, .bugbattle-double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #398cfe;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}

.bugbattle-double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

@-webkit-keyframes sk-bounce {
  0%, 100% {
    -webkit-transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

.bugbattle-screenshot-editor-container {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  height: -webkit-fill-available;
  z-index: 916777263;
}

.bugbattle-screenshot-editor-container-inner {
  position: relative;
  width: 100vw;
  height: 100vh;
  height: -webkit-fill-available;
}

.bugbattle-screenshot-editor-canvas {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  height: -webkit-fill-available;
  cursor: crosshair;
  z-index: 916777267;
}

.bugbattle-screenshot-editor-borderlayer {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  height: -webkit-fill-available;
  border: 12px solid #398cfe;
  cursor: crosshair;
  z-index: 916777272;
  box-sizing: border-box;
}

.bugbattle-screenshot-editor-dot {
  position: absolute;
  top: -16px;
  left: -16px;
  width: 16px;
  height: 16px;
  background-color: #398cfe;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 100%;
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.15);
  z-index: 916777270;
  box-sizing: border-box;
}

.bugbattle-screenshot-editor-drag-info {
  position: absolute;
  top: -100px;
  left: -100px;
  background-color: #398cfe;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  box-sizing: border-box;
  padding: 8px 10px;
  font-size: 16px;
  font-family: sans-serif;
  color: #fff;
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.15);
  z-index: 916777271;
  min-width: 270px;
  text-align: center;
}

.bugbattle-screenshot-editor-rectangle {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 0px;
  height: 0px;
  border: 5px solid #398cfe;
  box-sizing: border-box;
  z-index: 916777269;
}

@media only screen and (max-width: 600px) {
  .bugbattle--feedback-dialog {
    width: 100%;
    border-radius: 0px;
    box-sizing: border-box;
    max-width: inherit;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .bugbattle--feedback-dialog-header {
    border-radius: 0px;
  }
  .bugbattle--feedback-button-inner {
    width: 32px;
    height: 100px;
  }
  .bugbattle--feedback-button-inner-text {
    font-size: 15px;
  }
  .bugbattle-screenshot-editor-drag-info {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
  }
}
